import { SummitPage } from './../summit/summit';
import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ModalController } from 'ionic-angular';
import { Dashboard2Page } from '../dashboard2/dashboard2';
import { TabsPage } from '../tabs/tabs';


/**
 * Generated class for the ComplaintsPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-complaints',
  templateUrl: 'complaints.html',
})
export class ComplaintsPage {
  gender: string;
  netResponse: string;

  constructor(public navCtrl: NavController, public navParams: NavParams,  private modalCtrl: ModalController) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ComplaintsPage');
  }

  showModalA() {
    var modal = this.modalCtrl.create(SummitPage);
    modal.present();
  }

  goToTabsPage() {
    this.navCtrl.setRoot(TabsPage)
  }



}



